$templateCache.put("app/model/projects/dedup.html","<div class=\"jumbotron text-center dedup-jumbotron-bg\"></div><div class=\"container-fluid wow fadeInUp\"><div class=\"row text-center project-text-col\"><h1 class=\"wow fadeInDown\">{{projectsController.project.title}}</h1><h3 class=\"lead wow fadeInUp\">{{projectsController.project.description}}</h3></div><div id=images class=\"row proxee-text-col\"><div class=\"col-xs-12 col-sm-12 col-md-2 col-lg-2\"></div><div class=\"col-md-8 col-lg-8 project-content\"><h1 class=\"lead text-center\">OVERVIEW</h1><p>Dedup is a python program I created as apart of my Information Retrieval class. The premise relates to search engines and web crawlers. During the class, 3 group members and I built a web crawler to grab images and webpages from the internet. What\'s a web crawler? Web crawlers automatically grab/index content from the internet.</p><p>Essentially when web crawlers are indexing/crawling/downloading web pages on the internet there are times when the same data may be crawled (aka \"looked at\") repeatedly. Because of this you could end up downloading the same webpages, images or content; this can lead to unnecessary waste of memory or even worse, your crawler getting stuck (\'spider traps\'). Thus, deduplication is one technique used to get around these problems.</p><p>Feel free to check out the project on <a ng-href={{projectsController.project.url}} target=_blank>Github</a></p><h1 class=lead>MY TASKS</h1><p>To accomplish deduplication I:<ul><li><b>PROGRAM DESIGN:</b> Came up with a pipeline for a command line application</li><li><b>ALGORITHM DESIGN</b> Came up with a way to \'featurize\' images to efficiently compare them</li></ul></p><p>The entire program was written in <b>Python</b>. I chose Python because it\'s got an awesome abstraction for multiprocess programming that makes it quick write highly parallel programs.</p></div><div class=\"col-xs-12 col-sm-12 col-md-2 col-lg-2\"></div></div><div class=\"row text-center proxee-text-col\"><h1 class=lead>PIPELINE</h1><div class=\"col-xs-12 col-sm-12 col-md-12 col-lg-12\"><img class=dedup-limit src=assets/images/dedup-architecture.tiff alt=\"\"><h6><b>Figure 1:</b> The deduplication pipeline.</h6><br></div></div><br><div id=roommate class=\"row proxee-text-col\"><br><div class=\"col-md-2 col-lg-2\"></div><div class=\"col-xs-12 col-sm-12 col-md-8 col-lg-8 text-justify\"><p><b>Figure 1</b> shows the deduplication pipeline. This architecture allowed me to write a program that can deduplicate <b>~70000</b> images in about <b>10 seconds</b>.</p><br><h4 class=text-center><b>Techie Section:</b> What\'s happening behind the scenes?</h4><p><ul><li>Near De-duplication</li><li>Exact De-duplication</li><li>\"MapReduce\"</li></ul>It was extremely necessary to write a program that could deduplicate efficiently. No one wants to wait forever for a task such as this. To speed up the process, the python program:<ul><li><b>Split</b> Splits the data into M smaller segments</li><li><b>Workers</b> Creates M worker processes</li><li><b>Deduplicate</b> Tells each worker process to deduplicate it\'s smaller data chunk</li><li><b>Merge</b> Then merge the deduplicated images from the smaller sets</li></ul></p><p>The deduplication has two methodologies. One is \'exact\' deduplication, and the other is \'near\' deduplication. An exact duplicate set of images would have all the same exact pixels in the same exact positions. A nearly duplicate set of images could be the same images with slightly differing brightness, or slightly different pixel values.</p><h4>Exactly!</h4><p>Exact deduplication can be done fairly quickly, by simply \'hashing\' the image bytes and storing the hash/value pairs in a lookup table/dictionary. This ends up being extremely efficient, and you can iterate and deduplicate the data in one pass. Then add that to the fact that it\'s working on multiple cores/processes and you get a blazingly fast program.</p><h4>The end is Nigh... Near?</h4><p>Near deduplication is slightly different. In this case, you can\'t just simply \'hash\' the image. If the images are different in ANY way, and you hash them, the resultant hashes will be unbelievably different. That\'s a desired result of hashing, but it doesn\'t suit the problem we\'re trying to solve.</p><p>It would be nice if we could get similar hashes when the items were only slightly different. Turns out that some awesome people at Google came up with what\'s called <code>Simhash</code>. Essentially, it gives us the ability to reduce a huge piece of data (an image) to a small \'fingerprint\'! In addition to reducing the data, it also lets two pieces of data that are somewhat \'similar\' to have similar hashes (aka sim... hashes...). To generate the finger print I used features such as the image height, width, portions of the image bytes, etc.</p><p>I applied a similar high level logic to solving the near de-duplication problem. Instead of simply seeing if a hash value exists within the lookup table, I check to see if a \'nearly\' similar hash value exists within the lookup table. To speed this up even more, I use an index which sits on top of the data.</p><br></div><div class=\"col-md-2 col-lg-2\"></div></div></div>");
