$templateCache.put("app/model/projects/proxee.html","<div class=\"jumbotron project-jumbotron text-center proxee-jumbotron-bg\"></div><div class=\"container-fluid wow fadeInUp\"><div class=\"row text-center\"><h1 class=\"wow fadeInDown\">PROXEE</h1><h3 class=\"lead wow fadeInUp\">{{projectsController.project.description}}</h3></div><div id=images class=\"row proxee-text-col\"><div class=\"col-xs-12 col-sm-12 col-md-2 col-lg-2\"></div><div class=\"col-md-8 col-lg-8 project-content\"><h1 class=\"lead text-center\">OVERVIEW</h1><p>Proxee is an iOS app I started developing during the summer of 2015. A friend and I had an idea about making it easier for people to not only find great places to live, but to also find great people to live with.</p><h1 class=lead>WHAT I DID</h1><p>My role in this project was:<ul><li><b>DESIGN:</b> Coming up with a slick design and UX for the app</li><li><b>FRONT END:</b> Writing the app code and and bringing the design to life</li><li><b>BACK END</b> I wired up a simple backend on Parse to store user & apartment data</li></ul></p><p>To write the app, I picked up <b>Swift</b>. I hadn\'t programmed in Swift before, but I had experience in Objective-C prior to that. It was a really smooth transition from Obj-C to Swift. (I think Apple did an amazing job btw with the language!) We never launched the app to the app store, but it was a great experience writing the app. I\'m looking forward to the day when I completely launch my first app!</p></div><div class=\"col-xs-12 col-sm-12 col-md-2 col-lg-2\"></div></div><div class=\"row text-center\"><h1 class=lead>THE LOOK</h1><div class=\"col-xs-12 col-sm-12 col-md-6 col-lg-6\"><img class=project-limit src=assets/images/proxee.png alt=\"\"><h6><b>Figure 1:</b> The apartment listing feed.</h6><br></div><div class=\"col-xs-12 col-sm-12 col-md-6 col-lg-6\"><img class=project-limit src=assets/images/proxee-roommate.png alt=\"\"><h6><b>Figure 2:</b> Like Tinder... for roommate matching</h6><br></div></div><br><div id=roommate class=\"row proxee-text-col\"><br><div class=\"col-md-2 col-lg-2\"></div><div class=\"col-xs-12 col-sm-12 col-md-8 col-lg-8 text-justify\"><p><b>Figure 1</b> shows the apartment listing feed. The goal for this page was to make the focus all on the actual listings themselves. I took inspiration from Pinterest, and chose what\'s called a \'waterfall\' layout to let each apartment listing flow into the next. I was really interested in minimizing the details (price, bed/bath, etc.) just enough. Just enough so that they\'re legible, but not to large that they take focus off the real content.</p><br><h4 class=text-center><b>Techie Section:</b> What\'s happening behind the scenes?</h4><p><ul><li>Smooth Scrolling</li><li>MVVM - Model - View - View Model</li></ul>To make scrolling really fast and responsive I needed to asynchronously load images. Any task that required network IO was off loaded to background tasks. Definitely a standard practice to do this, but I think it\'s extremely important to note that fact.</p><p>To make it easy to manipulate apartment listing data and also create a separation of concerns between data and presentation, I adopted a design paradigm called MVVM (Model - View - View Model). I chosed this instead of the standard MVC (Model - View - Controller) to avoid really massive view controllers. Essentially, the MVC paradigm often tends to lead to Controllers that do EVERYTHING. Network calls, loading data into views, etc. MVVM is a nice way to separate data manipulation and view logic. Additionally, it allows you to reuse a lot of the same components extremely easily accross multiple views.</p><p><b>Figure 2</b> shows the roommate matching portion of the app. My logic for this page was to use a layout that most of our target demographic would be familiar with. People in their 20\'s are fairly aware of dating apps, so a Tinder like interface is both familiar and easy to use for that demo. \'Swipe Right\' if you think you could live with that person and swipe... yeah you get the idea.</p><br></div><div class=\"col-md-2 col-lg-2\"></div></div></div>");
